body{margin:0;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:linear-gradient(50deg, #282f31 0%, #2d4349 100%)}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}
.App{text-align:center;font-family:"Quicksand";min-height:100vh}input:focus,label:focus,button:focus{outline:none}
nav{margin-bottom:20px;display:flex;justify-content:space-between;align-items:center;box-shadow:0 5px 10px #171818;padding:7px 2%;background-color:rgba(0,0,0,.3)}nav p{border:solid 2px #3db6ac;border-radius:15px;background:linear-gradient(to right, #3db6ac 0%, #358d86 100%);color:#000;padding:5px 10px;box-shadow:0 1px 8px #171818;font-size:130%}nav p:hover{cursor:pointer;color:#171818;box-shadow:inset 0 0 3px #358d86;border-color:#358d86}
.logo-box .tilt{width:80px;height:80px;background:linear-gradient(to right, #3db6ac 0%, #358d86 100%);border:solid 2px #3db6ac;border-radius:15px;box-shadow:0 1px 8px #171818}.logo-box .tilt .logo-svg{padding-top:40px;fill:#3db6ac}
.form-box{width:auto;padding:0 2%}.form-box p{font-size:140%;font-weight:500;color:#000}.form-box .form-input{display:flex;justify-content:center}.form-box input{width:70%;padding:5px;color:#358d86;font-size:130%;border:solid 2px #2d4349;background:linear-gradient(to bottom, #1b2424 0%, rgba(0, 0, 0, 0.15) 100%);box-shadow:0 2px 8px #171818;border-radius:15px}.form-box button{width:100px;border:solid 2px #3db6ac;border-radius:15px;background:linear-gradient(to right, #3db6ac 0%, #358d86 100%);color:#000;padding:5px 10px;box-shadow:0 1px 8px #171818;font-size:140%;margin-left:10px}.form-box button:hover{cursor:pointer;color:#171818;box-shadow:inset 0 0 3px #358d86;border-color:#358d86}
.rank-box{width:35%;margin:40px auto;padding:10px}.rank-box .rank-text{font-size:140%;color:#3db6ac}.rank-box .rank-num{font-size:200%;color:#3db6ac;text-shadow:0 0 4px #358d86}
.facerecognition-box{display:flex;justify-content:center;margin-top:30px}.facerecognition-box .image-box{position:absolute}.facerecognition-box .image-box img{width:500px;height:auto}.facerecognition-box .image-box p{color:#171818}.facerecognition-box .image-box .bounding-box{position:absolute;box-shadow:0 0 10px #3db6ac inset;border:solid 2px #3db6ac;display:flex;flex-wrap:wrap;justify-content:center;cursor:pointer}
.signin-box{width:300px;height:auto;margin:0 auto;background:linear-gradient(to bottom, #1b2424 0%, rgba(0, 0, 0, 0.15) 100%);font-size:120%;padding:10px;padding-bottom:10px;box-shadow:0 2px 8px #171818;border-radius:15px}.signin-box form a{color:#3db6ac;font-size:90%}.signin-box form a:hover{color:#358d86}.signin-box form fieldset{border:solid 2px #282f31}.signin-box form fieldset legend{color:#3db6ac;font-size:170%;padding:0 10px}.signin-box form fieldset input{padding:3px;margin:10px auto;background:linear-gradient(to bottom, #1b2424 0%, rgba(0, 0, 0, 0.15) 100%);width:100%;color:#358d86;border:solid 2px #2d4349;border-radius:10px}.signin-box form fieldset label{font-size:80%;color:#358d86;width:100%}.signin-box form fieldset .button{border:solid 2px #3db6ac;border-radius:15px;background:linear-gradient(to right, #3db6ac 0%, #358d86 100%);color:#000;padding:5px 10px;box-shadow:0 1px 8px #171818;font-size:110%;width:100px}.signin-box form fieldset .button:hover{cursor:pointer;color:#171818;box-shadow:inset 0 0 3px #358d86;border-color:#358d86}

